.donate-page-block {
  padding: 130px 0 50px 0;
  .h2Header {
    text-align: center;
    display: block;
    margin-bottom: 50px;
  }
  .donate-page-block-info {
    border-bottom: 1px solid rgba(0, 0, 0, 0.24);
    padding-bottom: 20px;
    p {
      color: $main-light-text-color;
      font-size: 21px;
      font-weight: 700;
      margin-bottom: 25px;
      span {
        font-weight: 400;
      }
    }
  }

  .donate-page-form {
    width: 70%;
    margin: 0 auto;
    padding-top: 50px;
    .label-input {
      position: relative;
      display: flex;
      flex-direction: column;
      margin-bottom: 30px;
      span {
        color: $main-text-color;
        font-size: 21px;
        line-height: 34px;
      }
      input {
        position: relative;
        z-index: 1;
        height: 50px;
        font-size: 24px;
        font-weight: 600;
        line-height: 1.1;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
        border: 1px solid #eaeae8;
        padding: 0 20px 0 20px;
        &[type=number] {
          padding: 0 135px 0 20px;
        }

        &::placeholder {
          color: $gray-text-color;
          font-family: $mainfont;
          font-size: 22px;
          font-weight: 400;
          line-height: 1.2;
        }
      }

      .currency {
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $common-orange-color;
        font-family: $mainfont;
        font-size: 32px;
        font-weight: 400;
        line-height: 28px;
        width: 132px;
        height: 48px;
        border-left: 1px solid #eaeae8;
        background-color: $white-text-color;
        position: absolute;
        top: 35px;
        right: 1px;
        margin-bottom: 0;
      }
      .fast-choice {
        span {
          border-bottom: 1px solid $main-text-color;
          margin-right: 30px;
          cursor: pointer;
          font-weight: 500;
          &.active {
            color: $common-orange-color;
            border-bottom: 1px solid $common-orange-color;
          }
        }
      }
    }
    .button-wrapper {
      margin-top: 40px;
      a {
        color: $orange-text-color;
      }
      p {
        color: $main-text-color;
        font-size: 16px;
        line-height: 21px;
        margin-bottom: 20px;
      }
      .btn {
        margin: 0 auto;
      }
    }
  }
}

@media only screen and (max-width: $tabletWidth) and (min-width: $mobileWidth + 1) {
  .donate-page-block {
    padding: 120px 0 50px 0;
  }
}

@media only screen and (max-width: $mobileWidth) {
  .donate-page-block {
    padding: 100px 0 50px 0;
    .donate-page-form {
      width: 100%;
    }
  }
}